//main.js里注册的都可以全局使用
import 'babel-polyfill';
import Vue from 'vue';
import App from './App';
import router from './router';
import fastclick from 'fastclick';
import VueLazyload from 'vue-lazyload';
import 'assets/scss/index.scss';
import 'swiper/dist/css/swiper.css';

fastclick.attach(document.body);

Vue.use(VueLazyload, {
  preLoad: 1.3, //1 其实是小数  代表还差多少的时候加载   1是完全显示的时候加载
  error: require('assets/img/error.png'), //失败的时候显示的图片
  loading: require('assets/img/loading.gif'), //成功的时候显示的图片
  attempt: 1 //尝试加载几次
});

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
});



